import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subject } from 'rxjs';
import { Agent } from 'src/app/Model/Agent';
import { BankAccount } from 'src/app/Model/BankAccount';
import { Broker } from 'src/app/Model/broker';
import { Company } from 'src/app/Model/Company';
import { MotorType } from 'src/app/Model/MotorType';
import { Transactions } from 'src/app/Model/Transactions';
import { VehicleModel } from 'src/app/Model/VehicleModel';
import { AgentService } from 'src/app/services/agent/agent.service';
import { AlertService } from 'src/app/services/alert.service';
import { BankAccountsService } from 'src/app/services/BankAccounts/bankaccounts.service';
import { BranchService } from 'src/app/services/branch/branch.service';
import { BrokerService } from 'src/app/services/broker/broker.service';
import { CompanyService } from 'src/app/services/company/company.service';
import { MotorTypeService } from 'src/app/services/motortype/motor-type.service';
import { TransactionService } from 'src/app/services/transaction/transaction.service';
import { VehicleService } from 'src/app/services/vehiclemodel/vehicle.service';
import { BranchDTO } from '../../branch/view-branch/view-branch.component';

@Component({
  selector: 'app-refund-add',
  templateUrl: './refund-add.component.html',
  styleUrls: ['./refund-add.component.css']
})
export class RefundAddComponent implements OnInit {
  form :FormGroup;
  optionAgent:Agent[];
  optionBroker:Company[];
  optionsBank:BankAccount[];
  optionVehicleModel:VehicleModel[];
  optionMotorType:MotorType[];
  optionCompany:Broker[];
  loading=false;
  branches:BranchDTO[];
  @Input() transactionDataSubject:Subject<Transactions>;
  transactionId=0;
  constructor(private fb:FormBuilder,
    private agentService:AgentService,
    private companyService:CompanyService,
    private bankService:BankAccountsService,
    private _transactionService:TransactionService,
    private _branchService:BranchService,
    private _motorTypeService:MotorTypeService,
    private _vehicleModelService:VehicleService,
    private _notification:AlertService,
    private _brokerService:BrokerService) {
    this.generateForm();
  }
  generateForm()
  {
   this.form=this.fb.group({
     companyName:[null,[Validators.required]],
     transactionDate:[null,[Validators.required]],
     agentId:[null],
     companyId:[null],
     motorTypeId:[null],
     vehicleModelId:[null],
     policyNumber:[null,[Validators.required]],
     amount:[null,[Validators.required]],
      transactionType:[null,[Validators.required]],
      branchId:[null,[Validators.required]]

   });

  }
  get f () {return this.form.controls}
  ngOnInit(): void {

    this._branchService.branchObserver$.subscribe(res=>{

      this.branches=res?.branches;

    });
    this._branchService.GetBranches();
    this.agentService.agentObserve$.subscribe(res=>{
      this.optionAgent=res;
    })
    this.agentService.GetAgent();
    this.companyService.companyObserver$.subscribe(res=>{
      this.optionBroker=res;
    });

    this.companyService.GetCompanies();
    this.bankService.bankAccountsObserver$.subscribe(res=>{
      this.optionsBank=res;
    });
    this.bankService.GetBankAccounts();
    this._motorTypeService.motortypeObserve$.subscribe(res=>{
      this.optionMotorType=res;
    })
    this._motorTypeService.GetMotortypes();
    this._vehicleModelService.vehiclemodalObserve$.subscribe(res=>{
      this.optionVehicleModel=res;
    });
    this._vehicleModelService.GetVehicleModel();
    this._brokerService.brokerObserver$.subscribe(res=>{
      this.optionCompany=res;
    })
    this._brokerService.Getbrokers();

    this.transactionDataSubject.subscribe(res=>{
      if(res)
      {
        this.form.patchValue(res);
        console.log(res);
        this.transactionId=res.id;
        this.form.controls.transactionType.setValue(res.transactionType);

      }
      else
        {
          this.form.reset();
          this.transactionId=null;

        }
    });
  }

  formatDate(res) {
    var date = new Date(`${res} UTC`);
    return date
  }
  startChange(event:any)
  {
    var date = this.formatDate(event.value);
    this.form.controls.transactionDate.setValue(date.toISOString());
   console.log("startChange",event.value)
  }

  handleSubmit()
  {
    this.loading=true;
    let data = this.form.value as Transactions;
    data.id=this.transactionId;
    data.isRefund=true;
      this._transactionService.UpdateTransaction(data).subscribe(res=>{
        if(res.isSuccessfull){
          this.loading=false;
          this.generateForm()
          this._transactionService.GetTransaction();
          this._notification.success('Refund Transaction Saved Successfully!');
        }
        else{
          this._notification.error('Error while saving Transaction!');
        }
      })
    }

}
