import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { BehaviorSubject, Observable } from 'rxjs';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { BaseApiHandlerService } from '../base-api-handler.service';
import { Transactions } from 'src/app/Model/Transactions';
import { BankAccount } from "src/app/Model/BankAccount";
import { Agent } from 'src/app/Model/Agent';
import { Company } from 'src/app/Model/Company';

@Injectable({
  providedIn: 'root'
})
export class TransactionService extends BaseApiHandlerService{
  transactionObserver$:Observable<Transactions[]>;
  private transactionSubject$: BehaviorSubject<Transactions[]> = new BehaviorSubject<Transactions[]>(undefined);
  transactionForBankObserver$:Observable<Transactions[]>;
  private transactionForBankSubject$: BehaviorSubject<Transactions[]> = new BehaviorSubject<Transactions[]>(undefined);
  transactionForBrokerObserver$:Observable<Transactions[]>;
  private transactionForBrokerSubject$: BehaviorSubject<Transactions[]> = new BehaviorSubject<Transactions[]>(undefined);

  transactionForCompanyObserver$:Observable<Transactions[]>;
  private transactionForCompanySubject$: BehaviorSubject<Transactions[]> = new BehaviorSubject<Transactions[]>(undefined);
  transactionForAgentObserver$:Observable<Transactions[]>;
  private transactionForAgentSubject$: BehaviorSubject<Transactions[]> = new BehaviorSubject<Transactions[]>(undefined);

  bankaccountObserver$:Observable<BankAccount[]>;
  private bankaccountSubject$: BehaviorSubject<BankAccount[]> = new BehaviorSubject<BankAccount[]>(undefined);

  agentObserver$:Observable<Agent[]>;
  private agentSubject$: BehaviorSubject<Agent[]> = new BehaviorSubject<Agent[]>(undefined);

  companyObserver$:Observable<Company[]>;
  private companySubject$: BehaviorSubject<Company[]> = new BehaviorSubject<Company[]>(undefined);
  refundTransactionObserver$:Observable<Transactions[]>;
  private refundTransactionSubject$:BehaviorSubject<Transactions[]> = new BehaviorSubject<Transactions[]>(undefined);



  public get Transaction(){ return this.transactionSubject$.value}
  constructor(private http:HttpClient) {
    super(http);
    this.transactionObserver$= this.transactionSubject$.asObservable();
    this.companyObserver$= this.companySubject$.asObservable();
    this.agentObserver$ = this.agentSubject$.asObservable();
    this.bankaccountObserver$ = this.bankaccountSubject$.asObservable();
    this.transactionForCompanyObserver$=this.transactionForCompanySubject$.asObservable();
    this.transactionForAgentObserver$=this.transactionForAgentSubject$.asObservable();
    this.transactionForBankObserver$=this.transactionForBankSubject$.asObservable();
    this.transactionForBrokerObserver$=this.transactionForBrokerSubject$.asObservable();
    this.refundTransactionObserver$=this.refundTransactionSubject$.asObservable();


  }
  SaveTransaction(data){
    return this.Post(data,API_URL+API_ENDPOINTS.Transaction);
  }
  RefundTransaction(data)
  {
    return this.Post(data,API_URL+API_ENDPOINTS.Transaction+"/RefundTransaction");
  }
  UpdateTransaction(data:Transactions){
    return this.Update(data.id,data,API_URL+API_ENDPOINTS.Transaction);
  }
//#region Refund
  GetRefundTransactions(page,pageSize)
  {
    return this.GetAll(API_URL+API_ENDPOINTS.Transaction+`/GetRefundTransactions?page=${page}&pageSize=${pageSize}`)
      .subscribe(res=>{
        this.refundTransactionSubject$.next(res.dynamicResult);
      });
  }
////#endregion

  SearchBrokerTransaction(search){
    return this.GetAll(API_URL+API_ENDPOINTS.Transaction+`/Search?search=${search}&isBroker=true`)
    .subscribe(res=>{
      this.transactionForBrokerSubject$.next(res.dynamicResult);
    });
  }
  SearchRefundTransaction(search){
    return this.GetAll(API_URL+API_ENDPOINTS.Transaction+`/Search?search=${search}&isRefund=true`)
    .subscribe(res=>{
      this.refundTransactionSubject$.next(res.dynamicResult);
    });
  }
  SearchAgentTransaction(search){
    return this.GetAll(API_URL+API_ENDPOINTS.Transaction+`/Search?search=${search}&isAgent=true`)
    .subscribe(res=>{
      this.transactionSubject$.next(res.dynamicResult);
    });
  }
  SearchBankTransaction(search){
    return this.GetAll(API_URL+API_ENDPOINTS.Transaction+`/Search?search=${search}&isBank=true`)
    .subscribe(res=>{
      this.transactionForBankSubject$.next(res.dynamicResult);
    })
  }
  GetTransactionForCompany()
  {
    return this.GetAll(API_URL+API_ENDPOINTS.TransactionForCompany).subscribe(res =>{
      this.transactionForCompanySubject$.next(res.dynamicResult);
    });
  }
  GetTransactionForAgent()
  {
    return this.GetAll(API_URL+API_ENDPOINTS.TransactionForAgent).subscribe(res =>{
      this.transactionForAgentSubject$.next(res.dynamicResult);
    });
  }
  GetTransaction(page=1,pageSize=20){
    return this.GetAll(API_URL+API_ENDPOINTS.Transaction+`/GetPaginated?page=${page}&pageSize=${pageSize}&isBroker=${false}
    &isAgent=${true}&isBank=${false}`
    
    ).subscribe(res =>{
      this.transactionSubject$.next(res.dynamicResult);
    });
  }
  GetBrokerTransaction(page=1,pageSize=20){
    return this.GetAll(API_URL+API_ENDPOINTS.Transaction+`/GetPaginated?page=${page}&pageSize=${pageSize}&isBroker=${true}
    &isAgent=${false}&isBank=${false}`
    
    ).subscribe(res =>{
      this.transactionForBrokerSubject$.next(res.dynamicResult);
    });
  }
  GetBankTransaction(page=1,pageSize=20){
    return this.GetAll(API_URL+API_ENDPOINTS.Transaction+`/GetPaginated?page=${page}&pageSize=${pageSize}&isBroker=${false}
    &isAgent=${false}&isBank=${true}`
    
    ).subscribe(res =>{
      this.transactionForBankSubject$.next(res.dynamicResult);
    });
  }
  GetBankaccount(data)
  {
    return this.GetAll(API_URL+API_ENDPOINTS.BankAccount).subscribe(res=>{
      this.bankaccountSubject$.next(res.dynamicResult);
    });
  }
  GetCompany(data)
  {
    return this.GetAll(API_URL+API_ENDPOINTS.Company).subscribe(res=>{
      this.companySubject$.next(res.dynamicResult);
    });
  }

  GetCounts()
  {
    return this.GetAll(API_URL+API_ENDPOINTS.Transaction+"/GetCounts");
  }
  GetAgent(data)
  {
    return this.GetAll(API_URL+API_ENDPOINTS.Users).subscribe(res=>{
      this.agentSubject$.next(res.dynamicResult);
    });
  }
  DeleteTransaction(id){
    return this.Delete(id,API_URL+API_ENDPOINTS.Transaction);
  }  
}
