import { Component, OnInit } from '@angular/core';
import { Observable, Subject, Subscription,from } from 'rxjs';
import { AlertService } from 'src/app/services/alert.service';
import { Transactions, TransactionType } from "src/app/Model/Transactions";
import { TransactionService } from "src/app/services/transaction/transaction.service";
import { BankAccountsService } from 'src/app/services/BankAccounts/bankaccounts.service';
import { AgentService } from 'src/app/services/agent/agent.service';
import { CompanyService } from 'src/app/services/company/company.service';

@Component({
  selector: 'app-view-transaction',
  templateUrl: './view-transaction.component.html',
  styleUrls: ['./view-transaction.component.css']
})
export class ViewTransactionComponent implements OnInit {
  serach:string;
  listData: Transactions[]=[];
  isVisible = false;
  refundVisible=false;
  refundModalTitle="Refund old Transactions";
  showRefundTrans=false;
  modalTitle = "Create new Transaction";
  TransactionObserSubject: Subject<Transactions> = new Subject();
  contactSubject: Subscription;
  checked = false;
  showTransaction = false;
  transaction = new Transactions();
  public transactionType=TransactionType;
  pageSize=20;
  totalItems=0;

  constructor(private _TransactionService: TransactionService,
    private _bankAccountService:BankAccountsService,
    private _agentService:AgentService,
    private _companyService:CompanyService,
    private _notification: AlertService) {     }


    Search()
    {
      this._TransactionService.SearchBrokerTransaction(this.serach);
    }

   ngOnInit(): void {
    this._TransactionService.GetCounts().subscribe(res=>{
      this.totalItems=res.dynamicResult?.brokerCount;
      
      console.log(this.totalItems)
    });
    this._TransactionService.transactionForBrokerObserver$.subscribe(res=>{
      if (res) {
        this.listData = res.filter(x=>x.agentId==null);
        console.log(this.listData)
      }
      this.isVisible = false;
    });
 
    this._TransactionService.GetBrokerTransaction(1,20);
    this._bankAccountService.GetBankAccounts();
    this._agentService.GetAgent();
    this._companyService.GetCompanies();
  }
  showModal(): void {
    this.isVisible = true;
    this.modalTitle = "New Transaction";
    this.TransactionObserSubject.next(null);
    this.showTransaction = false;
  }
  pageIndexChange($event)
    {
      console.log($event);
      let page=$event;
       this._TransactionService.GetBrokerTransaction(page,this.pageSize);
    }
  handleCancel() {
    this.isVisible = false;
    this.showTransaction = false;
  }
  handleOpen() {
    this.isVisible = true;
    this.modalTitle = "New Sale";
    this.TransactionObserSubject.next(null);
    this.showTransaction = false;
  }
  showRefundModal(): void {
    this.isVisible = true;
    this.modalTitle = "New Transaction";
    this.TransactionObserSubject.next(null);
    this.showTransaction = false;
  }
  handleRefundOpen() {
    this.refundVisible=true;
    this.refundModalTitle="Refund old Transactions";
    this.showRefundTrans=false;
    // this.isVisible = true;
    // this.modalTitle = "New Sale";
    // this.TransactionObserSubject.next(null);
    // this.showTransaction = false;
  }
  handleRefundCancel() {
    this.refundVisible=false;
    this.refundModalTitle="Refund old Transactions";
    this.showRefundTrans=false;
  }
  handleEdit(data: Transactions) {
    this.modalTitle = "Edit Transaction";
    this.isVisible = true;
    this.showTransaction = false;
    this.TransactionObserSubject.next(data);
  }
  showSingleTransaction(data:Transactions) {
    this.transaction = data;
    this.showTransaction = true;
   
    this.showTransaction = true;
  }
  DeleteTransaction(data: Transactions) {
    this._notification.delete().then(result => {
      if (result.isConfirmed) {
        this._TransactionService.DeleteTransaction(data.id).subscribe(res => {
          if (res.isSuccessfull) {
            this._TransactionService.GetTransaction();
            this._notification.success('Transaction Deleted sucessfully!')
          }
          else {
            this._notification.error('Error while Deleteing Transaction!')
          }
        });
      }
      else {
        return;
      }

      this.showTransaction = false;
    });
  }
}
