import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { BankAccount } from 'src/app/Model/BankAccount';
import { BaseResponse } from 'src/app/Model/base-response';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { BaseApiHandlerService } from '../base-api-handler.service';

@Injectable({
  providedIn: 'root'
})
export class BankAccountsService extends BaseApiHandlerService {

  bankAccountsObserver$: Observable<BankAccount[]>;
  private bankAccountsSubject$: BehaviorSubject<BankAccount[]> = new BehaviorSubject<BankAccount[]>(undefined);

  public get bankAccounts() { return this.bankAccountsSubject$.value; }
  constructor(private httpClient: HttpClient) {
    super(httpClient);
    this.bankAccountsObserver$ = this.bankAccountsSubject$.asObservable()
  }
  

  SaveBankAccount(data) {
    return this.Post(data, API_URL + API_ENDPOINTS.BankAccount );
  }

  UpdateBankAccount(data: BankAccount) {
    return this.Update(data.id, data, API_URL + API_ENDPOINTS.BankAccount);
  }

  GetBankAccounts() {
    this.GetAll(API_URL + API_ENDPOINTS.BankAccount).subscribe(res => {
      this.bankAccountsSubject$.next(res.dynamicResult);
    });
  }

  DeleteBankAccount(id): Observable<BaseResponse> {
    return this.Delete(id, API_URL + API_ENDPOINTS.BankAccount);
  }
  
}
